.fancybox-bg{
    background: rgba(0, 0, 0, .7);
}
.fancybox-slide--html{
    padding: 10px 0;
}
.fancybox-button.fancybox-close-small{
    width: 28px;
    height: 28px;
    padding: 0;
    right: 22px;
    top: 22px;
}
.fancybox-content{
    padding: 0;
}
.fancybox-can-pan .fancybox-content, .fancybox-can-swipe .fancybox-content{
    cursor: auto;
}
.modal{
    background-color: var(--White);
    border-radius: 10px;
    &__inner{
        padding: 55px 60px;
        .section-title{
            margin-bottom: 20px;
            text-align: left;
        }
        p{
            color: var(--Charcoal);
            font-size: 13px;
            line-height: 1.4;
        }
        .Btn{
            padding: 10px 26px 9px;
            min-width: 152px;
        }
        .alert{
            margin: 0 0 30px;
            font-size: 13px;
            color: var(--mainColor);
        }
        .input{
            padding-left: 140px;
            &-wrap{
                position: relative;
                &::before{
                    position: absolute;
                    content: "";
                    height: calc(100% - 20px);
                    width: 1px;
                    background-color: var(--Gainsboro);
                    left: 126px;
                    top: 10px;
                }
                textarea.input{
                    height: 100px;
                    min-height: 39px;
                    overflow: hidden;
                    min-width: 100%;
                }
            }
        }
        .bottom{
            border-top: 1px dashed var(--Gainsboro);
            margin-top: 20px;
            padding-top: 10px;
            .Btn-Wedgewood{
                margin-top: 5px;
            }
        }
    }
}
.log__Wrap{
    margin-top: 30px;
    display: flex;
    align-items: center;
    .Btn-Wedgewood{
        margin-right: 30px;
    }
}
#makeOrder,#modalThanks,#send-resume,#order-service{
    width: 660px;
    display: none;
    .modal__inner .bottom .Btn{
        width: 158px;
    }
}
#modalThanks{
    .Btn{
        min-width: auto;
        padding: 9px 22px;
        margin-top: 10px;
    }
}
#logIn, #passw-reuest,#oneClick{
    width: 490px;
    display: none;
}
.compensate-for-scrollbar{
    .body__inner{
        margin-right: 17px;
    }
}
#findCheaper {
    width: 490px;
    display: none;
}
.view-product {
    display: none;
    position: fixed;
    left: 0;
    top: 0;
    background-color: var(--White);
    width: 100%;
    height: 100vh;
    box-sizing: border-box;
    z-index: 6;
    .mCSB_inside>.mCSB_container {
        margin-right: 0;
    }
    &__inner {
        display: flex;
        position: relative;
        height: 100%;
        padding: 50px 30px;
        button {
            position: absolute;
            top: 50px;
            right: 200px;
            color: var(--Charcoal);
            font-size: 18px;
            z-index: 2;
            cursor: pointer;
        }
    }
    .swiper-container {
        height: 100%;
    }
    .swiper-button-prev::after, .swiper-button-next::after {
        font-size: 27px;
    }
    .bx-e10, .bx-e11 {
        top: calc(50% - 10px);
    }
    .swiper-button-prev, .swiper-button-next {
        top: calc(50% + 10px);
    }
    .swiper-button-prev {
        left: 195px;
    }
    .swiper-button-next {
        right: 195px;
    }
    .card-img {
        margin-bottom: 0;
    }
    .gallery-top {
        width: calc(100% - 120px);
        border-right: 1px dashed var(--dashed);
        .swiper-slide {
            display: flex;
            align-items: center;
            justify-content: center;
        }
        .card-img {
            width: 804px;
            height: 444px;
        }
    }
    .gallery-thumbs {
        width: 90px;
        height: 100%;
        padding-left: 30px;
        box-sizing: content-box;
        flex-shrink: 0;
        .swiper-wrapper {
            flex-direction: column;
        }
        .swiper-slide {
            width: 90px;
            height: 90px;
            margin-bottom: 20px;
            border: 1px solid var(--Gainsboro);
            cursor: pointer;
            transition: all 0.2s;
            &:hover {
                border: 1px solid var(--Wedgewood);
            }
        }
        .swiper-slide-thumb-active {
            border: 1px solid var(--Wedgewood);
        }
    }
    .swiper-button-prev, .swiper-button-next, .swiper-button-prev::after, .swiper-button-next::after {
        width: 21px;
    }
}
.min-modal{
    position: fixed;
    top: 99px;
    z-index: 3;
    background-color: #fff;
    width: 425px;
    border-radius: 10px;
    transition: all .3s ease-out;
    transform: translateY(-100%);
    opacity: 0;
    visibility: hidden;
    &__inner{
        display: flex;
        align-items: flex-start;
        flex-direction: column;
        padding: 30px;
        position: relative;
        width: 100%;
    }
    i.close{
        position: absolute;
        right: 30px;
        font-size: 15px;
        top: 30px;
        margin-right: 0;
        cursor: pointer;
    }
    .section-title{
        font-size: 17px;
        margin-bottom: 20px;
        margin-right: 20px;
    }
    &__img{
        max-width: 116px;
        width: 100%;
        height: 65px;
        margin-right: 10px;
        background-position: center;
        background-size: cover;
    }
    &__body{
        margin-bottom: 20px;
        display: flex;
        width: 100%;
        &-inner{
            width: 100%;
            flex-shrink: 1;
        }
        p{
            margin: 0;
            color: #494949;
            font-size: 14px;
            line-height: 21px;
            width: 100%;
        }
    }
    &__bottom{
        display: flex;
        .Btn-Flamenco1{
            margin-right: 10px;
        }
        .Btn-Wedgewood{
            padding-right: 20px;
            padding-left: 20px;
        }
    }
    &.modal-show{
        opacity: 1;
        visibility: visible;
        transform: translateY(0);
    }
    &.elect{
        right: 260px;
    }
    &.comparison{
        right: 200px;
    }
    &.basket{
        width: 490px;
        right: 45px;
        .min-modal__img{
            height: 80px;
        }
        strong{
            margin-top: 6px;
            display: block;
        }
    }
}
.header-fixed.f_show + .min-modals{
    .min-modal{
        top: 70px;
        &.elect{
            right: 124px;
            transition: all .3s ease-out;
        }
        &.comparison{
            right: 60px;
        }
        &.basket{
            right: 200px;
        }
    }

}
#send-resume {
    .bottom {
        padding-top: 0;
        border-top: none;
        button {
            padding: 8px 39px;
        }
    }
    .input-wrap:last-child {
        margin-top: 20px;
    }
    .input-wrap:before {
        left: 141px;
    }
    .input {
        padding-left: 155px;
    }
    .input-List label:last-child input {
        border: 1px solid var(--Wedgewood);
    }
    .Btn {
        padding: 9px 22px 9px;
    }
}
#contacts-btn_send {
    display: none;
    width: 658px;
    .Btn {
        width: auto;
        min-width: auto;
        padding: 9px 23px 9px;
        font-size: 15px;
    }
    h2 {
        margin-bottom: 15px;
        line-height: 1.2;
    }
    .alert {
        margin-bottom: 40px;
    }
}
@media(max-width: 1550px) {
    .view-product {
        .swiper-button-prev {
            left: 100px;
        }
        .swiper-button-next {
            right: 100px;
        }
    }
}
@media(max-width: 1350px) {
    .view-product {
        &__inner {
            padding: 30px;
            button {
                top: 30px;
                right: 190px;
            }
        }
        .gallery-top {
            .card-img {
                width: 650px;
                height: 370px;
            }
        }
    }
}
@media(max-width: 1199px){
    .min-modal{
        &.elect{
            right: 280px;
        }
    }
}
@media(max-width: 1150px) {
    .view-product {
        &__inner {
            flex-direction: column;
            button {
                right: 30px;
            }
        }
        .gallery-top {
            width: 100%;
            border-right: none;
            padding: 50px 15px 40px;
            border-bottom: 1px dashed var(--dashed);
            box-sizing: border-box;
        }
        .gallery-thumbs {
            width: 100%;
            height: 80px;
            padding-left: 0;
            padding-top: 30px;
            .swiper-wrapper {
                flex-direction: row;
                justify-content: center;
            }
            .swiper-slide {
                width: 80px;
                height: 100%;
                margin: 0;
                &:not(:last-child) {
                    margin-right: 10px;
                }
            }
        }
        .swiper-button-prev {
            left: 0;
        }
        .swiper-button-next {
            right: 0;
        }
    }
}
@media(max-width: 1024px){
    .compensate-for-scrollbar{
        .body__inner{
            margin-right: 0;
        }
    }
}
@media(max-width: 991px){
    .min-modal{
        &.elect,&.comparison, &.basket{
            right: 15px;
            top: auto;
            bottom: 15px;
        }
    }
    .header-fixed.f_show + .min-modals{
        .min-modal{
            &.elect, &.comparison, &.basket{
                right: 15px;
                top: auto;
            }
        }
    }
}
@media(max-width: 850px) {
    .view-product {
        .gallery-top {
            padding: 35px 0 20px;
            .card-img {
                width: 100%;
            }
        }
        .gallery-thumbs {
            padding-top: 20px;
            height: 55px;
            .swiper-slide {
                width: 55px;
            }
            .swiper-slide:not(:last-child) {
                margin-right: 5px;
            }
        }
    }
}
@media(max-width: 767px){
    .min-modal__inner{
        padding: 20px;
        right: 0;
    }
}
@media(max-width: 700px){
    #contacts-btn_send {
        width: 290px;
        .modal__inner {
            padding: 40px 20px 40px;
        }
        h2 {
            font-size: 19px;
            margin-bottom: 8px;
        }
        .alert {
            margin-bottom: 28px;
        }
    }
}
@media(max-width: 600px){
    .fancybox-button.fancybox-close-small{
        right: 8px;
        top: 8px;
    }
    #makeOrder, #modalThanks, #findCheaper, #send-resume, #order-service{
        width: 290px;
        .file-wrap{
            margin-top: 0;
        }
    }
    #logIn, #passw-reuest,#oneClick{
        width: 290px;
    }
    #logIn{
        .input-List{
            margin-bottom: 15px;
        }
    }
    .modal{
        &__inner{
            padding: 25px 20px 30px;
            .section-title{
                margin-bottom: 10px;
            }
            .top{
                 p{
                     margin-bottom: 20px;
                 }
            }
            .bottom{
                margin-top: 10px;
                padding-top: 0;
            }
            .input-wrap::before{
                display: none;
            }
            .input{
                padding-left: 10px;
            }
        }
    }
    .log__Wrap{
        flex-direction: column;
        align-items: flex-start;
        .Btn-Wedgewood{
            margin-right: 0;
            margin-bottom: 18px;
        }
    }
    .min-modal{
        right: 0!important;
        bottom: 15px!important;
        width: 320px!important;
        i.close{
            top: 20px;
            right: 20px;
        }
        &__img{
            display: none;
        }
        &__bottom{
            flex-direction: column;
            width: 100%;
            .Btn-Flamenco1{
                margin-right: 0;
                margin-bottom: 10px;
                text-align: center;
            }
            .Btn-Wedgewood{
                text-align: center;
            }
        }
    }
    .header-fixed.f_show + .min-modals{
        .min-modal{
            &.elect, &.comparison, &.basket{
                bottom: 45px!important;
            }
        }
    }
    #send-resume {
        .input {
            padding-left: 10px;
        }
        .input-wrap:last-child {
            margin-top: 10px;
        }
        .input-List label:last-child span {
            top: -6px;
        }
    }
}
@media(max-width: 400px) {
    .view-product {
        .gallery-top {
            padding: 35px 5px 20px;
            height: 213px;
            .card-img {
                height: 158px;
            }
        }
        .view-product__inner {
            padding: 20px;
            button {
                font-size: 15px;
                right: 20px;
                top: 20px;
            }
        }
    }
}